.add-social-media form {
    width: fit-content;
    height: fit-content;
    margin: 0 auto;
    padding: 50px;
    border-radius: 20px;
    background-color: rgb(0, 0, 0, 0.7);
    border: 2px solid white;

}

.add-social-media form td {
    height: 30px;
}

.add-social-media form input {
    background-color: transparent;
    border: 0px;
    border-bottom: 3px solid white;
    width: 120px;
    color: white;
    margin: 0px 5px;
}

.add-social-media form button {
    background-color: white;
    color: rgb(255, 71, 86);
    border: 0px;
    padding: 2px 5px;
}

.add-social-media form button:hover {
    background-color: white;
    color: green;
}

.added-socials {
    margin-top: 20px;
    text-align: center;
    background-color: rgba(255, 71, 86, 0.696);
    margin-bottom: 50px;
}

.added-socials .heading {
    text-align: center;
    height: 40px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(255, 71, 86);
}


.added-socials .individual-social-media {
    display: grid;
    grid-template-columns: 10% 30% 30% 30%;
    height: 30px;
    background-color: rgb(0, 0, 0, 0.5);
}

.added-socials .individual-social-media button {
    border: 0px;
    background-color: rgb(255, 71, 86);
    color: whitesmoke;
    padding: 3px 5px;
    position: relative;
    top: 2px;
}

.add-social-media .form-heading {
    text-align: center;
    font-weight: bold;
    color: white;
}

.add-social-media .form1 {
    margin-bottom: 20px;
}


.add-social-media .PhoneInput {
    width: 115px;
    display: grid;
    grid-template-columns: 47px 70px;
    overflow: hidden;
    border-bottom: 3px solid WHITE;
    position: relative;
    left: 10px;
}

.add-social-media .PhoneInput input {
    position: relative;
    border-bottom: 0px;
    right: 33px;
    width: 95px;

}

.add-social-media .PhoneInput .PhoneInputCountry {
    font-size: 0px;
    margin-right: 10px;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-areas: 'icon select';
    border: 1px solid transparent;
    height: 18px;
    width: 20px;

}

.add-social-media .PhoneInput .PhoneInputCountry .PhoneInputCountryIcon {
    position: relative;
    height: 20px;
    grid-area: icon;
}

.add-social-media .PhoneInput .PhoneInputCountry .PhoneInputCountrySelect {
    font-size: 0px;
    height: 16px;
    width: 17px;
    text-align: center;
    position: absolute;
    z-index: 10;
    opacity: 0;
}

@media all and (max-width: 309px) {
    .add-social-media .social-media-form-container form {
        padding: 50px 0px;
        width: 100vw;
    }

    .add-social-media .social-media-form-container form table {
        width: fit-content;
        margin: 0 auto;
    }

    .add-social-media .social-media-form-container form table td input {
        width: 50vw;
    }
}

@media all and (max-width: 280px) {
    .forAddSocialMediaOnly {
        overflow-x: scroll;
    }

    section.added-socialss {
        background-color: transparent;
        overflow: scroll;
    }

    .add-social-media .bookings-info-form-container form {
        width: 100vw;
        padding: 50px 0px;
    }

    .add-social-media .bookings-info-form-container form table {
        width: fit-content;
        margin: 0 auto;
    }

    .added-socialss .individual-social-media {
        grid-template-columns: 100px;
        min-height: 100px;
        border: 1px solid white;
        text-align: center;
        width: fit-content;
        margin: 0 auto;
        overflow: scroll;
    }
}

@media all and (max-width: 189px) {
    .added-socials {
        overflow: hidden;
    }

    .add-social-media form {
        overflow: scroll;
    }

    .add-social-media .bookings-info-form-container form table input,
    .add-social-media .bookings-info-form-container .PhoneInput {
        width: 50vw;
    }

    .add-social-media .bookings-info-form-container .PhoneInput {
        position: relative;
        right: 5PX;
    }

    .add-social-media .social-media-form-container form {
        width: 125px;
    }

    .add-social-media .social-media-form-container form table td input {
        width: 30%;
    }

}